import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Cell } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osName = platform();

const Exam = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="useful">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderBack>}>
			ЕГЭ 2021
		</PanelHeader>
		<Cell multiline before={'24.05, пн \u00A0 \u00A0'}>География, литература и химия</Cell>
		<Cell multiline before={'27.05, чт \u00A0 \u00A0'}>Русский язык</Cell>
		<Cell multiline before={'31.06, пн \u00A0 \u00A0'}>Математика</Cell>
		<Cell multiline before={'03.06, чт \u00A0 \u00A0'}>Физика и история</Cell>
		<Cell multiline before={'07.06, пн \u00A0 \u00A0'}>Обществознание</Cell>
		<Cell multiline before={'10.06, чт \u00A0 \u00A0'}>Письменная часть по иностранным языкам и биология</Cell>
		<Cell multiline before={'15.06, вт \u00A0 \u00A0'}>Устная часть по иностранным языкам</Cell>
		<Cell multiline before={'16.06, ср \u00A0 \u00A0'}>Устная часть по иностранным языкам</Cell>
		<Cell multiline before={'18.06, пт \u00A0 \u00A0'}>Информатика</Cell>
		<Cell multiline before={'19.06, сб \u00A0 \u00A0'}>Информатика</Cell>
		<br/>
		<br/>
		<br/>
	</Panel>
);

Exam.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Exam;